.categories {
  margin-top: 180px;
}

.categories-slider {
  overflow: visible !important;
}

.categories-swiper {
  padding-top: 85px;
  position: relative;
}

.categories__item {
  padding: 32px;
  background: #3d2942;
  max-width: 415px;
  // transition: all 0.4s;

  &:hover {
    .categories__img {
      img {
        opacity: 0.5;
        z-index: 1;
      }
    }
    .categories__img-btn {
      background-color: #95782e;
      display: block;
      z-index: 5;
      cursor: pointer;
      opacity: 1;
      transition: all 0.3s;
      &:hover{
        background-color: $yellow;
      }
    }
  }
}

.categories__img {
  position: relative;
  overflow: hidden;
  transition: all 0.3s;

  img {
    object-fit: cover;
    transition: all 0.3s;
  }

  &-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 246px;
    max-height: 70px;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: $black;
    padding: 20px 57px;
    background-color: $yellow;
    border: 0px;
    white-space: nowrap;
    opacity: 0;
    transition: all 0.3s;
  }
}

.categories__profile {
  margin: 30px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.categories__user {
  display: flex;
  align-items: center;

  &-img {
    border-radius: 50%;
    overflow: hidden;
  }

  &-content {
    margin-left: 17px;
  }

  &-text {
    font-weight: 500;
    font-size: 28px;
    line-height: 35px;
  }

  &-nick {
    font-weight: 400;
    font-size: 20px;
    line-height: 35px;
    color: $white-gray;
  }
}

.categories__like {
  display: flex;
  align-items: center;

  &-text {
    font-size: 32px;
    line-height: 35px;
    color: $purple2;
    margin: -3px 0px 0px 10px;
  }
}

//---------------------------------------

.categories__bid {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-text {
    font-size: 24px;
    line-height: 35px;
    color: $gray;
  }
  &-price {
    font-family: 'Oxanium-Bold';
    font-size: 24px;
    line-height: 35px;
    color: $white1;
  }
}

.categories__btn {
  margin-top: 40px;
  padding: 21px 0;
  width: 100%;
  background: rgba(233, 215, 167, 0.1);
  border: 1px solid #fde8b3;
  color: $white;
  font-weight: 500;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: 0.05em;
  cursor: pointer;
}
