@media (max-width: 1250px) {
  .first__title {
    font-size: 80px;
    line-height: 90px;
  }

  .first__buttons {
    margin-bottom: 75px;
  }

  .first__list {
    gap: 35px;
  }

  .first__Nft {
    max-width: 450px;
    max-height: 530px;

    &-bottom {
      padding: 20px 15px 20px 25px;
    }
  }

  .first__top-span {
    font-size: 16px;
    line-height: 17px;
  }

  .first__top-text {
    font-size: 25px;
    line-height: 30px;
  }

  //-----------------companies----------------

  .companies {
    &__list {
      margin: 0;
    }
  }

  //--------------auctions--------------

  .auctions-slide {
    margin-right: 20px;
  }

  .auctions__item {
    padding: 15px;
    max-width: 450px;
  }

  //-------------------------

  .how__box-images::after {
    transform: scale(0.7);
    left: 85%;
  }

  //-----------------popular------------------

  .popular__item {
    padding: 15px;
  }

  .popular__user-nick {
    font-size: 26px;
    margin-left: 5px;
  }

  .popular__text {
    font-size: 18px;
  }
  //--------------------categories-------------
  .categories__item {
    padding: 15px;
  }

  .categories__img {
    img {
      width: 100%;
    }
  }

  .categories__user {
    &-content {
      margin-left: 5px;
    }
  }
  .categories__user-text {
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;
  }

  .categories__user-nick {
    font-size: 17px;
    line-height: 27px;
  }

  .categories__bid {
    &-text {
      font-size: 20px;
      line-height: 19px;
    }
    &-price {
      font-family: Oxanium-Bold;
      font-size: 20px;
      line-height: 25px;
      color: #f5fbf2;
    }
  }
  //------------------creators---------------

  .creators__box {
    padding: 25px 15px 30px;
  }

  .creators__user-content {
    margin-left: 10px;
  }
}

@media (max-width: 1000px) {
  .first__title {
    font-size: 55px;
    line-height: 80px;
  }
  .first__buttons {
    gap: 10px;
    &-link {
      padding: 20px 60px;
      font-size: 23px;
      line-height: 30px;
    }
  }

  .first__item-num {
    font-size: 45px;
    line-height: 45px;
  }

  .first__item-text {
    font-size: 20px;
    line-height: 25px;
  }

  .first__arrow-link {
    display: none;
  }

  //-----------------companies----------------

  .companies {
    &__list {
      display: grid;
      grid-template-columns: 2fr 2fr;
      gap: 20px;
      text-align: center;
    }
  }
  //--------------------auctions-------------

  .auctions-box-prev {
    left: 85%;
  }

  //-----------------how------------------
  .how__items {
    overflow-y: hidden;
  }

  .how__box-images {
    margin-bottom: 10px;
    &::after {
      transform: scale(0.6);
      left: 75%;
    }
  }

  .how__box-title {
    font-size: 20px;
    line-height: 15px;
  }
  //---------------creators------------
  .creators__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 55px;
    max-width: 600px;
    margin: 0 auto;
  }

  .creators__box {
    width: 100%;
  }
  //--------------------footer-------------

  .footer__content {
    max-width: 300px;
  }

  .footer__lists {
    gap: 30px;
  }
}
//-----------------900--------------
@media (max-width: 900px) {
  .header {
    position: fixed;
    left: 0;
    right: 0;
    z-index: 5;
    top: 0;
    padding: 25px 0px;
    background-color: rgba(0, 0, 0, 0.2);

    &__main {
      margin: 0;
    }

    &__logo {
      order: 1;
    }

    &__btn {
      order: 2;
    }
  }

  .nav {
    order: 3;
    margin: 0;

    &__link {
      font-size: 25px;
    }
  }

  //---------NAV---------

  .nav__list {
    position: fixed;
    top: 0;
    right: -10px;
    // bottom: 0;
    width: 40%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: rgba(#1c1c1c, 0.9);
    z-index: -1;
    padding: 85px 0px 0px 24px;
    justify-content: space-evenly;
    transform: translateX(100%);
    transition: transform 0.4s;
    overflow: auto;

    &--active {
      transform: translateX(0);
    }
  }

  //-------BURGER--------

  .nav {
    &__burger {
      cursor: pointer;
      &::after,
      &::before {
        content: '';
        display: block;
        width: 24px;
        height: 2.4px;
        background-color: $yellow;
        transition: transform 0.4s;
      }
    }

    &__burger.nav__burger--active {
      span {
        display: none;
      }
      &::after {
        content: '';
        transform: rotate(-45deg);
      }
      &::before {
        content: '';
        transform: translateY(2px) rotate(45deg);
      }
    }

    &__burger {
      &-line {
        display: block;
        width: 24px;
        height: 2px;
        background-color: $yellow;
        margin: 4px 0px;
      }
    }
  }
  //-----------------------First------------

  .first__title {
    font-size: 45px;
    line-height: 60px;
  }

  .first__Nft-top {
    padding: 15px;
  }

  .first__top-span {
    font-size: 13px;
    line-height: 14px;
  }

  .first__top-text {
    font-size: 20px;
    line-height: 20px;
  }

  .first__bottom-text {
    font-size: 20px;
    line-height: 20px;
  }

  .first__bottom-nik {
    font-size: 17px;
    line-height: 25px;
  }

  .first__bottom-btn {
    padding: 20px 30px;
    font-size: 20px;
    line-height: 20px;
  }

  //------------------------------

  .auctions__img-text {
    padding: 10px 35px;
  }

  .auctions__profile {
    margin: 20px 0;
  }

  .auctions__user-text {
    font-size: 22px;
    line-height: 25px;
  }

  .auctions__user-nick {
    font-size: 16px;
    line-height: 25px;
  }

  .auctions__bid-text {
    font-size: 20px;
  }

  .auctions__bid-price {
    font-size: 19px;
  }
  //----------------how--------------

  .how__items {
    flex-direction: column;
    gap: 110px;
  }

  // .how__box-images{
  //   margin-bottom: 10px;
  // }

  .how__box-images::after {
    // transform: scale(.6);
    transform: rotate(90deg) scale(0.6);
    top: 170%;
    left: -30%;
    // bottom: 80%;
  }
  //----------popular---------------
  .popular {
    margin-top: 70px;
  }
  //---------------categories----------
  .categories {
    margin-top: 70px;
  }

  //------------creators----------------
  .creators {
    margin-top: 70px;
  }

  //------------subscribe---------------
  .subscribe {
    padding: 60px 10px;

    &__title {
      line-height: 80px;
      margin-bottom: 30px;
    }

    &__form {
      flex-direction: column;
      &-btn {
        margin-top: 25px;
        padding: 25px 45px;
      }
    }
  }
  //------------------footer---------------
  .footer__image-logo{
    width: 112px;
  }

  .footer__content {
    max-width: 250px;
  }
}

//---------------800------------
@media (max-width: 750px) {
  .first__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .first__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .first__title {
    max-width: 350px;
  }

  .first__Nft {
    margin-top: 50px;
    max-width: 282px;
    min-height: 380px;
    &-inner {
      height: 380px;
    }
  }

  //---------------auctions------------------
  .auctions-box-prev {
    left: 82%;
  }
  .auctions-slide {
    display: flex;
    justify-content: center;
  }
  //------------popular--------------------
  .popular-slide {
    display: flex;
    justify-content: center;
  }
  //-----------------footer----------------
  .footer__inner {
    padding: 80px 0;
    flex-direction: column;
    align-items: center;
  }

  .footer__image-logo {
    margin: 31px 0;
  }

  .footer__content{
    max-width: 100%;
    text-align: center;

    .footer__content-list{
      display: none;
    }
    .footer__content-bottom{
      display: none;
    }
  }

  .footer__lists{
    margin-top: 30px;
    width: 100%;
    justify-content: space-evenly;
  }

  .footer__content-fake{
    display: block;
  }

  .footer__content-list {
    margin: 30px 0;
  }

  .footer__content-bottom{
    font-size: 20px;
    line-height: 36px;
    color: $purple;
  }
}
