.footer__inner{
  padding: 115px 0;
  display: flex;
  align-items: start;
  justify-content: space-between;
}

.footer__content-fake{
  display: none;
}

.footer__content{
  max-width: 390px;
  font-size: 20px;
  line-height: 36px;
  color: $purple;

  &-text{
  font-weight: 500;
  }

  &-list{
    display: flex;
    align-items: center;
    margin: 50px 0;
  }

  &-bottom{
    font-weight: 400;
  }
}

.footer__list-link{
  margin-right: 30px;
  path{
    transition: all .3s;
  }
  &:hover{
    path{
      fill: $white;
    }
  }
}

//--------------------------------

.footer__lists{
  display: flex;
  gap: 80px;
}

.footer__about-title,
.footer__Support-title{
  color: $white1;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
  margin-bottom: 20px;
}

.footer__about-item,
.footer__Support-item{
  margin-bottom: 10px;
}

.footer__about-link,
.footer__Support-link{
  color: $purple;
  font-weight: 500;
  font-size: 20px;
  line-height: 50px;
  transition: all .3s;

  &:hover{
    color: $white1;
  }
}