*,
*::after,
*::before {
    box-sizing: border-box;
}

html{
  overflow-x:hidden;
}

a{
  color: inherit;
  text-decoration: none;
}


body {
  font-family:'WorkSans-Medium' , sans-serif;
  font-size: 16px; 
  line-height: 20px;
  font-weight: 500;
  color: $white1;
  background-color: #1C121F;
  overflow-x: hidden;
}

.container {
  max-width: 1370px;
  padding: 0 15px; 
  margin: 0 auto; 
  height: 100%;
  overflow-x: hidden;
}

.button-empty{
  background: rgba(233, 215, 167, 0.1);
  border: 1px solid #FFCE4E;
  cursor: pointer;
  transition: all .4s;
  &:hover{
    background: $yellow;
    color: $black;
    border: 1px solid $black;
  }
}

.button-yellow{
  background-color: $yellow;
  color: $black;
  cursor: pointer;
  transition: all .3s;
  border: 1px solid;
  &:hover{
    background: rgba(233, 215, 167, 0.1);
    color: $white;
    border: 1px solid $yellow;
  }
}

.title{
  text-align: center;
  font-family: "Oxanium-Bold"; 
  color: $white1;
  font-size: 64px;
  line-height: 130px;
}

.title-purple{
  position: relative;
  // margin-bottom: 85px;
  &::before{
    content: '';
    background-image: url(/img/purple.png);
    background-position: center;
    background-repeat: no-repeat;
    transform: translateX(-35%);
    position: absolute;
    width: 110px;
    height: 110px;
    z-index: -1;
  }
}

.title-red{
  position: relative;
  // margin-bottom: 85px;
  &::before{
    content: '';
    background-image: url(/img/red.png);
    background-position: center;
    background-repeat: no-repeat;
    transform: translateX(-35%);
    position: absolute;
    width: 110px;
    height: 110px;
    z-index: -1;
  }
}

.View-btn{
  display: block;
  margin: 80px auto 0;
  font-weight: 500;
  font-size: 30px;
  line-height: 35px;
  letter-spacing: 0.05em;
  color: $white1;
  padding: 25px 65px;
}