.first {
  margin-top: 140px;
  position: relative;
}

.first__inner {
  display: flex;
  justify-content: space-between;
}

.first__title {
  max-width: 700px;
  font-family: 'Oxanium-Bold';
  font-weight: 700;
  font-size: 85px;
  line-height: 130px;
  span {
    color: $yellow;
  }
}

.first__buttons {
  margin: 75px 0 120px;
  display: flex;
  align-items: center;
  grid-gap: 45px;

  &-link {
    padding: 25px 75px;
    font-weight: 600;
    font-size: 28px;
    line-height: 36px;
  }
}

.first__list {
  display: flex;
  align-items: center;
  gap: 100px;
  &-item {
    text-align: center;
  }
}

.first__item-num {
  font-weight: 700;
  font-size: 62px;
  line-height: 78px;
}

.first__item-text {
  font-weight: 400;
  font-size: 24px;
  line-height: 35px;
  color: $purple;
}

//-------------------------------------

.first__Nft {
  margin-left: 20px;
  background-image: url(/img/first/NFT.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 558px;
  width: 100%;
  max-height: 720px;

  &-inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
}

.first__Nft-top {
  font-family: 'Oxanium-Bold';
  margin: 23px 23px 0;
  padding: 17px 45px;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(
    92.8deg,
    rgba(255, 137, 61, 0.09) -52.37%,
    rgba(250, 250, 250, 0.9) 137.41%
  );
  opacity: 0.95;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
  backdrop-filter: blur(22.5px);
}

.first__top-span {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.08em;
  text-transform: capitalize;
}

.first__top-text {
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: $white;
}

.first__Nft-bottom {
  background: #fff;
  backdrop-filter: blur(13.5px);
  padding: 35px 30px 35px 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.first__bottom {
  &-text {
    font-family: 'Oxanium-Bold';
    font-weight: 600;
    font-size: 28px;
    line-height: 35px;
    letter-spacing: 0.07em;
    color: #16091b;
  }
  &-nik {
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: #594d5e;
  }
}

.first__bottom-btn {
  padding: 23px 45px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: $black;
  transition: all 0.4s;
  border: none;
  &:hover {
    background-color: #16091b;
    border: none;
  }
}

.first__arrow {
  position: absolute;
  left: 50%;
  bottom: 12%;

  &-link {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    background: rgba(109, 91, 114, 0.4);
    width: 150px;
    height: 150px;
    border-radius: 50%;
    box-shadow: 0px 14px 79px rgba(134, 19, 165, 0.1);
    backdrop-filter: blur(20px);
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background-color: $purple;
    }
  }
}
