.how__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.how__box {
  display: flex;
  flex-direction: column;
  align-items: center;

  &-images {
    background: #372839;
    border-radius: 50%;
    width: 106px;
    height: 106px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 130%;
      // transform: translate(-50%);
      background-image: url(../../img/how/line.svg);
      background-position: center;
      background-repeat: no-repeat;
      width: 173px;
      height: 10px;
    }
  }

  &:last-child {
    .how__box-images {
      &::after {
        display: none;
      }
    }
  }

  &-title {
    color: $yellow;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    text-transform: capitalize;
  }
}
