.subscribe{
  padding: 115px 0;
  background-image: url(/img/subscribeBG.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.subscribe__form{
  display: flex;
  align-items: center;
  max-width: 680px;
  margin: 0 auto;

  &-text{
    width: 100%;
    padding: 30px 30px 30px 50px;
    border: 1px solid #E7B226;
    font-weight: 400;
    font-size: 24px;
    line-height: 35px;
    color: $white;
    outline: 0;
    background: transparent;
    &::placeholder{
      color: #A48EA9;
    }
  }

  &-btn{
    font-family: "WorkSans-Bold";
    background-color: $yellow;
    padding: 30px 63px;
    font-weight: 700;
    font-size: 24px;
    line-height: 35px;
    color: #251928;
    border: 1px solid $yellow;
  }
}