.creators{
  margin-top: 185px;
}

.creators__inner{
  margin-top: 80px;
  display: grid;
  grid-template-columns: 2fr 2fr;
  gap: 55px;
}

.creators__box{
  background: #302034;
  box-shadow: inset 2px 0px 0px #E03054;
  max-width: 600px;
  padding: 25px 65px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.creators__user{
  display: flex;
  align-items: center;
}

.creators__user-content{
  margin-left: 25px;
}

.creators__user-text{
  font-family: "Oxanium-Bold"; 
  font-weight: 600;
  font-size: 32px;
  line-height: 35px;
  letter-spacing: 0.05em;
  color: $white1;
}

.creators__user-price{
  color: $white-gray;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
}

.creators__item{
  padding-left: 45px;
  border-left: 3px solid #49374E;
  text-align: center;

  &-num{
    font-family: "Oxanium-Bold";
    font-size: 32px;
    line-height: 35px;
    letter-spacing: 0.05em;
  }

  &-text{
    color: #908294;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
  }
}