.popular {
  margin-top: 165px;
  // .container{

  // }
}

.popular-slider {
  position: relative;
}

.popular__item {
  max-width: 525px;
  padding: 35px;
  background: #3d2942;
}

.popular__img {
  overflow: hidden;
}

.popular__profile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 25px;
}

.popular__user {
  display: flex;
  align-items: center;

  &-nick {
    font-weight: 500;
    font-size: 32px;
    line-height: 36px;
    color: $white1;
    margin-left: 15px;
  }
}

.popular__text {
  font-weight: 600;
  font-size: 20px;
  line-height: 35px;
  color: #c2b5c6;
}

.popular-box-prev,
.popular-box-next {
  position: absolute;
  opacity: 0.5;
  transition: all 0.4s;
  &::after {
    display: none;
  }
  &:hover {
    opacity: 1;
  }
}

.popular-box-prev {
  left: 15px;
}
.popular-box-next {
  right: 15px;
}

.popular-button-next {
  transform: rotate(180deg);
}
