.header{
  font-size: 18px;
  line-height: 21px;
  &__main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 37px;
  }

  &__btn{
    color: $yellow;
    padding: 22px 30px;
    transition: all .2s;
    &:hover{
      background: rgba(233, 215, 167, 0.1);
      color: $white1;
      border: 1px solid $white1;
    }
  }
}

.nav{
  margin: 0 160px 0 auto;
  &__list{
    display: flex;
    align-items: center;
    grid-gap: 60px;
  }

  &__link{
    font-family: "WorkSans-Medium", sans-serif;
    letter-spacing: 0.02em;
    transition: all .3s;
    padding-bottom: 8px;
    &:hover{
        color: $yellow;
        border-bottom: 1px solid;
    }
  }
}

