.auctions{
  padding-bottom: 80px;
}

.auctions-slider{
  overflow: visible !important;
}

.auctions-swiper{
  padding-top: 85px;
  position: relative;
}

.auctions__item{
  padding: 32px;
  background: #3D2942;
  max-width: 495px;
}

.auctions__img{
  position: relative;
  overflow: hidden;

  img{
    object-fit: cover;
  }

  &-text{
    position: absolute;
    bottom: 5px;
    padding: 15px 45px;
    font-size: 24px;
    line-height: 35px;
    letter-spacing: 0.02em;
    background: linear-gradient(125.33deg, #8613A5 18.61%, rgba(250, 250, 250, 0.65) 137.4%);
    opacity: 0.95;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
    backdrop-filter: blur(22.5px);
  }
}

.auctions__profile{
  margin: 30px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.auctions__user{
  display: flex;
  align-items: center;

  &-img{
    border-radius: 50%;
    overflow: hidden;
  }

  &-content{
  margin-left: 17px;
  }

  &-text{
    font-weight: 500;
    font-size: 28px;
    line-height: 35px;
  }

  &-nick{
  font-weight: 400;
  font-size: 20px;
  line-height: 35px;
  color: $white-gray;
}
}

.auctions__like{
  display: flex;
  align-items: center;

  &-text{
    font-size: 32px;
    line-height: 35px;
    color: $purple2;
    margin: -3px 0px 0px 10px;
  }
}

//---------------------------------------

.auctions__bid{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &-text{
    font-size: 24px;
    line-height: 35px;
    color: $gray;
  }
  &-price{
    font-family: "Oxanium-Bold"; 
    font-size: 24px;
    line-height: 35px;
    color: $white1;
  }
}

.auctions__btn{
  margin-top: 40px;
  padding: 21px 0;
  width: 100%;
  background: rgba(233, 215, 167, 0.1);
  border: 1px solid #FDE8B3;
  color: $white;
  font-weight: 500;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: 0.05em;
  cursor: pointer;
  transition: all 0.3s;
  &:hover{
    background: $yellow;
    color: #000;
    border: 1px solid #000;
  }
}

//--------------------------------

.auctions-box-prev,
.auctions-box-next{
  position: absolute;
  top: 30px;
  left: 95%;
  opacity: 0.5;
  transition: all 0.4s;
  &::after{
    display: none;
  }
  &:hover{
    opacity: 1;
  }
}

.auctions-box-prev{
  top: 35px;
  left: 89%;
}

.auctions-button-next{
  transform: rotate(180deg);
}